import React, { Component } from 'react';
import { BrowserRouter as Router, NavLink } from "react-router-dom";
import logo from '../../images/logo.png';
import { getTranslate, getActiveLanguage, getLanguages } from 'react-localize-redux';
import { connect } from 'react-redux';
import { setActiveLanguage } from 'react-localize-redux';

/*
const store = createStore(combineReducers({
    locale: localeReducer
})); */

class Navbar extends Component {


    render() {
        
        return (
        
            
            <header>
            
                <div className="row menu-head">
                    <div className="container-fluid">
                        <div className="col-sm-2 logo">
                            <a href="/">
                                <img src={logo} alt="logo" className="img_responsive" />
                            </a>
                        </div>
                        <div className="col-sm-8 menu">
                            <nav className="navbar navbar-inverse navbar-static-top">

                                <div className="navbar-header">
                                    <button className="navbar-toggle" data-target="#menu" data-toggle="collapse">
                                        <span className="icon-bar"></span>
                                        <span className="icon-bar"></span>
                                        <span className="icon-bar"></span>
                                    </button>
                                </div>
                                <div className="collapse navbar-collapse pull-right" id="menu">
                                    <ul className="nav navbar-nav">
                                        <li><NavLink exact to="/" data-toggle="collapse" data-target=".navbar-collapse">{this.props.translate('home')}</NavLink></li>
                                        <li><NavLink to="/missio" data-toggle="collapse" data-target=".navbar-collapse">{this.props.translate('mission')}</NavLink></li>
                                        <li><NavLink to="/yritys" data-toggle="collapse" data-target=".navbar-collapse">YRITYSESITTELYT</NavLink></li>
                                        <li><NavLink to="/taustaa" data-toggle="collapse" data-target=".navbar-collapse">{this.props.translate('background')}</NavLink></li>
                                        <li><NavLink to="/henkilot" data-toggle="collapse" data-target=".navbar-collapse">{this.props.translate('keypersonnel')}</NavLink></li>
                                        <li><NavLink to="/yhteystiedot" data-toggle="collapse" data-target=".navbar-collapse">{this.props.translate('contact')}</NavLink></li>
                                    </ul>
                                </div>
                            </nav>
                        </div>
                        <div className="col-sm-2 call">
                        <button  onClick={() => this.props.setActiveLanguage('fi')}>FI</button>
                    <button  onClick={() => this.props.setActiveLanguage('en')}>EN</button>
                            <a href="tel:0405450666"> <span className="glyphicon glyphicon-earphone"></span></a>
                        </div>
                    </div>
                </div>

            </header>
        );
    }
}

const mapStateToProps = state => ({
    translate: getTranslate(state.locale),
    currentLanguage: getActiveLanguage(state.locale).code,
    languages: getLanguages(state.locale)
});
const mapDispatchToProps = { setActiveLanguage };

//export default connect()(Navbar);
export default connect(mapStateToProps, mapDispatchToProps, null, {pure:false})(Navbar);






