import React, { Component } from 'react';
import '../../footer.css';
import { getTranslate, getActiveLanguage, getLanguages, setActiveLanguage } from 'react-localize-redux';
import { connect } from 'react-redux';


class Footer extends Component {


    render() {
        return (
           
            <footer>
            <div className="container-fluid">
            <div className="row">
            <div className="col-sm-2 contact">
                    <h2>{this.props.translate('contact')} </h2>
            </div> 
            <div className="col-sm-3 location">
                  <a href="https://www.google.fi/maps/place/H%C3%A4meenkatu+26,+20700+Turku/@60.4488949,22.2711316,17z/data=!3m1!4b1!4m5!3m4!1s0x468c76fb4311edf7:0x9bdd6eb67d63aa3f!8m2!3d60.4488949!4d22.2733203?hl=en"
                   target="_blank" rel="noopener noreferrer"> 
                   <span className="glyphicon glyphicon-map-marker pull-left"></span> </a>
                    <p>Mad Booster Oy <br />
                    &nbsp; &nbsp;  &nbsp;Hmeenkatu 26 B <br />
                    &nbsp; &nbsp; &nbsp;  FI-20700 Turku </p> 
            </div> 
            <div className="col-sm-3 location mailinfo">
            <a href="mailto:mail@madbooster.com" target="_top"><span className="glyphicon glyphicon-envelope pull-left"></span></a>
                    <p> &nbsp; mail@madbooster.com</p>
                    <a href="tel:0405450666"> <span className="glyphicon glyphicon-phone-alt pull-left"></span></a>
                    <p>&nbsp; +358405450666</p>
            </div>
            <div className="col-sm-4 location social-network">
                   <div> <a href="https://www.facebook.com/MadBoostercom/" target="_blank" rel="noopener noreferrer"><i className="fa fa-facebook"></i></a> </div>
                   {/*div><a href="" target="_blank"><i className="fa fa-twitter"></i></a></div> */ }
                   <div><a href="https://www.linkedin.com/company/madbooster/" target="_blank" rel="noopener noreferrer"><i className="fa fa-linkedin"></i> </a> </div>
                   <div><a href="https://www.youtube.com/channel/UCb_e0cwkp0PP7irW2PRQdiQ" target="_blank" rel="noopener noreferrer"><i className="fa fa-youtube"></i>  </a> </div>    
            </div>             

            </div>
            <div className="copyrights">
                       <p>Copyrights  &copy; All Rights Reserved at MadBooster Oy </p>
            </div>
            </div>
         </footer>
         
             );
            }
     }
                           

     const mapStateToProps = state => ({
        translate: getTranslate(state.locale),
        currentLanguage: getActiveLanguage(state.locale).code,
        languages: getLanguages(state.locale)
    });
    const mapDispatchToProps = { setActiveLanguage };
    
    export default connect(mapStateToProps, mapDispatchToProps, null, {pure:false})(Footer);


