import React, { Component } from 'react';
/*import member from '../../images/member.jpg';   */
import '../../henkilot.css';
import { getTranslate, getActiveLanguage, getLanguages, setActiveLanguage } from 'react-localize-redux';
import { connect } from 'react-redux';


class Henkilot extends Component {


    render() {
        return (
            <div>
            <div className="contact-head kaynti">
                <h2>{this.props.translate('avainhenkilot')}</h2>

                <p className="subhead"> {this.props.translate('avain-subhead')}</p>
                <hr />
            </div>
            <div className="member-top team">
            <div className="row ">
            
          
            <div className="col-sm-6 member contact-head kaynti">
                {/* <img src={member} alt="member" className="img_responsive" /> */}


                 <h2> JONI FORSBOM, CEO</h2>   
                 <p> {this.props.translate('joni-p')}</p>
                 <p className="member-mail">
                 {this.props.translate('email')}: joni.forsbom@madbooster.com <br />
                 {this.props.translate('phone')}: 040 5450 666
                 </p>
                 
            </div>
            <div className="col-sm-6 member contact-head kaynti">
                 {/* <img src={member} alt="member" className="img_responsive" /> */}
                 <h2> JUHA SAARIKOSKI, COO</h2>   
                 <p> {this.props.translate('juha-p')}
                 </p>
                 <p className="member-mail">
                 {this.props.translate('email')}: juha.saarikoski@madbooster.com <br />
                 {this.props.translate('phone')}: 0400 626 369
                 </p>
            </div>
            
           


            </div>
            </div>
            <div className="member-btm team">
            <div className="row">
            <div className="container-fluid">
            <div className="col-sm-4 member contact-head kaynti">
                  {/* <img src={member} alt="member" className="img_responsive" /> */}
                 <h2> TOMI NOKKALA, CTO</h2>   
                 <p> 
                 {this.props.translate('tomi-p')}</p>
                 <p className="member-mail">
                 {this.props.translate('email')}: tomi.nokkala@madbooster.com <br />
                 {this.props.translate('tomi-phone')}
                 </p>
            
            </div>
            <div className="col-sm-4 member contact-head kaynti">
                  {/* <img src={member} alt="member" className="img_responsive" /> */}
                 <h2> JARMO KHKNEN</h2>   
                 <p> {this.props.translate('jarmo-p')}</p>
                 <p className="member-mail">
                 {this.props.translate('email')}: jarmo.kahkonen@madbooster.com <br />
                 {this.props.translate('phone')}: 040 5484 152
                 </p>
            </div>
            <div className="col-sm-4 member contact-head kaynti">
              {/* <img src={member} alt="member" className="img_responsive" /> */}
                 <h2> KYSTI MOISIO</h2>   
                 <p> {this.props.translate('kyosti-p')}</p>
                 <p className="member-mail">
                 {this.props.translate('email')}: kyosti.moisio@madbooster.com <br />
                 {this.props.translate('phone')}: 040 3100 122
                 </p>
            </div>
            </div>

            </div>
            </div>

            </div>
             );
            }
     }
                           
     const mapStateToProps = state => ({
        translate: getTranslate(state.locale),
        currentLanguage: getActiveLanguage(state.locale).code,
        languages: getLanguages(state.locale)
    });
    const mapDispatchToProps = { setActiveLanguage };
    
    //export default connect()(Navbar);
    export default connect(mapStateToProps, mapDispatchToProps, null, {pure:false})(Henkilot);


