import React, { Component } from 'react';
import '../../yhteystiedot.css';
import { getTranslate, getActiveLanguage, getLanguages, setActiveLanguage } from 'react-localize-redux';
import { connect } from 'react-redux';


class Yhteystiedot extends Component {


    render() {
        return (
            <div>
            <div className="contact-head kaynti">
                <h2>{this.props.translate('contact-us')}</h2>

              
                <hr />
            </div>
            <div className="container-fluid contact-sec">
                <div className="row">
                    <div className="col-sm-4 kaynti">
                        <h2>{this.props.translate('visit-add')}</h2>
                        
                        <hr />
                        <p className="info-bold">Mad Booster Oy</p>
                        <p> Regus Business Center <br />
                            Lntinen Pitkakatu 33, 3rd floor <br />
                            FI-20100 Turku <br />
                            Y-tunnus: 2798321-1</p>
                        <p className="info-bold"> mail@madbooster.com</p>
                    </div>
                    
                    <div className="col-sm-4 kaynti">
                        <h2>{this.props.translate('bill-add')}</h2>
                        <hr />
                        <p className="info-bold">Mad Booster Oy</p>
                        <p> Hmeenkatu 26 B <br />
                            FI-20700, Turku </p>
                    </div>
                    
                    <div className="col-sm-4 kaynti">
                    <h2>{this.props.translate('invoices')}</h2>
                    <hr />
                    <p>003727983211/MAVENTA <br />
                       (pankkiverkosta DABAFIHH)  </p>
                    </div>
                </div>
                <div className="kaynti">
                    <h2> {this.props.translate('getting')}</h2>
                    <p>HMEENKATU 26B, FI-20700, TURKU</p>
                </div>
                <div className="map">
                <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d1967.7771193365006!2d22.27113161641049!3d60.448894882064565!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x468c76fb4311edf7%3A0x9bdd6eb67d63aa3f!2sH%C3%A4meenkatu+26%2C+20700+Turku!5e0!3m2!1sen!2sfi!4v1520944984643"
                  align="center" allowfullscreen title="This is a unique title"></iframe>
                </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => ({
    translate: getTranslate(state.locale),
    currentLanguage: getActiveLanguage(state.locale).code,
    languages: getLanguages(state.locale)
});
const mapDispatchToProps = { setActiveLanguage };

//export default connect()(Navbar);
export default connect(mapStateToProps, mapDispatchToProps, null, {pure:false})(Yhteystiedot);



