import React, { Component } from 'react';
import '../../yritys.css';
import validus from '../../images/validus.png';
import spiderlogo from '../../images/spider_logo.png';
import { getTranslate, getActiveLanguage, getLanguages, setActiveLanguage } from 'react-localize-redux';
import { connect } from 'react-redux';

class Yritys extends Component {


    render() {
        return (
            <div className="container yritys">
                <h2>YRITYSESITTELYT</h2>
                <hr />
               {/* <p>  I'm a title. Click here to add your own text and edit me.</p> */}
               <div className="company">
                
                            <div className="top-box">
                            <img src={validus} alt="validus" />
                            <p><a href="http://www.validus.fi/" target="_blank">Validus Solutions Oy</a> on Mad Boosterin tytryhti ja sen tehtvn on tuottaa nykyaikaisia selaimessa toimivia sovelluksia palveluna. Ensimmiset tuotteet ovat jo pilottikytss ja ne keskittyvt henkilstn hallintaan ja ohjaukseen. <br /> <br />

                                Palvelumalli ja listiedot Spiderin omilla www-sivuilla. <a href="http://www.validus.fi/" target="_blank">www.validus.fi</a>
                            </p>  
                            </div>

                            
                    </div>
             

               <div className="company">
                
                <div className="btm-box top-box ">
                <img src={spiderlogo} alt="spiderlogo" />
                <p><a href="http://www.spidersolutions.fi/" target="_blank">Spider Solutions Oy</a> liittyi Mad Boosterin osakkuusyhtiksi lokakuussa 2017. Spiderin perustajat Joona Iso-Lotila ja Tomi Savolainen olivat kehittneet terveydenhuoltoalalle toimintamallia, jolla asiakkaat sstvt aikaa ja tehostavat potilastyt. Tss kohtaa kumppaniksi valikoitui Mad Booster, joka toteutti jrjestelmn toimintamallista tuotteeksi. 
                     <br /> <br />
                     Joona Iso-Lotila: "Yhteisty on toiminut alusta saakka saumattomasti, tuotekehitys on ollut ketter ja lopullinen tuote on jopa enemmn, kuin alkuun osasimme mritellkkn. Aikataulun puolesta Mad Booster on pystynyt toteuttamaan alle kahdessa kuukaudessa ensimmisen pilottiversion ja tarvittavat muutokset ovat mys syntyneet nopeammin kuin on ennalta arvioitu." 
                     <br /> <br />                  

                    Palvelumalli ja listiedot Spiderin omilla www-sivuilla. <a href="http://www.spidersolutions.fi/" target="_blank">www.spidersolutions.fi</a>
                </p>  

                
                </div>
        </div>
                
            </div>
        );
    }
}

const mapStateToProps = state => ({
    translate: getTranslate(state.locale),
    currentLanguage: getActiveLanguage(state.locale).code,
    languages: getLanguages(state.locale)
});
const mapDispatchToProps = { setActiveLanguage };

export default connect(mapStateToProps, mapDispatchToProps, null, {pure:false})(Yritys);



