import React, { Component } from 'react';
import idea from '../../images/idea.png';
import rohkea from '../../images/rohkea.png';
import yhdessa from '../../images/yhdessa.png';
import '../../missio.css';
import { getTranslate, getActiveLanguage, getLanguages, setActiveLanguage } from 'react-localize-redux';
import { connect } from 'react-redux';


class Missio extends Component {


    render() {
        return (
            <div>
            <div className="container">
            <div className="mission-head">
            <h2>{this.props.translate('mission-head')}</h2>
            <hr />
            <p className="subhead"> 
            {this.props.translate('mission-subhead')}
            </p>  
            </div>
            <div className="row idea">
                <div className="col-sm-6 pull-right">
                <img src={idea} alt="idea" className="img_responsive"/>
                </div>
                <div className="col-sm-6 infobox missio pull-left">
                        <h2>{this.props.translate('ideasta')}</h2>
                        <p>{this.props.translate('idea-p')}</p>
                </div>  
                <div className="clearfix"> </div>  
            </div>
            
            <div className="row idea rohkea">
                <div className="col-sm-6">
                <img src={rohkea} alt="rohkea" className="img_responsive" />
                </div>
                <div className="col-sm-6 infobox missio missio1">
                        <h2>{this.props.translate('rohkea')}</h2>
                        <p>{this.props.translate('rohkea-p')}</p>
                </div>  
                <div className="clearfix"> </div>  
            </div> 
            <div className="row idea yhdessa ">
                <div className="col-sm-6 pull-right">
                <img src={yhdessa} alt="yhdessa" className="img_responsive" />
                </div>
                <div className="col-sm-6 infobox missio2 pull-left">
                        <h2>{this.props.translate('yhdessa')}</h2>
                        <p>{this.props.translate('yhdessa-p')}</p>
                </div>  
                <div className="clearfix"> </div>  
            </div>
        </div>
        
         </div>
             );
             }
      }
                            
      const mapStateToProps = state => ({
        translate: getTranslate(state.locale),
        currentLanguage: getActiveLanguage(state.locale).code,
        languages: getLanguages(state.locale)
    });
    const mapDispatchToProps = { setActiveLanguage };
    
    //export default connect()(Navbar);
    export default connect(mapStateToProps, mapDispatchToProps, null, {pure:false})(Missio);



