module.exports = {"home":"HOME","mission":"MISSION","partner":"PARTNER","background":"BACKGROUND","keypersonnel":"KEY PERSONNEL","contact":"CONTACT US","background-1":"Mad Booster LLC wants to be a part of creating new success stories. We want to build responsible business, where motivated and determined people make bold decisions and work together. We want to simply be the best. ","background-2":"Behind success is invariably a lot of work. Work itself is not an intrinsic value, and in itself does not guarantee anything. Only when working systematically towards a set goal, can good results be expected. A successful company is more than the sum of its parts. Behind one is almost always vocational excellence in its core divisions.","background-3":"All success stories are different - each resembles its creator. However, there are certain similarities in all success stories. In corporate life, success is measured through profits.Thats a good thing, since excellent results are the only facilitators of continuous growth and development. When a good business idea is surrounded by the right team committed to working responsibly and persistently towards a goal, results are achieved.","mission-head":"WHY YOU SHOULD CHOOSE US","mission-subhead":"Mad Boosters mission is to work as an active partner to developing companies and to enable their controlled growth and financial success.","ideasta":"FROM THOUGHT TO REALITY","idea-p":"The better the idea, the easier it is to bring to life. We can help with the realisation, because even a good idea requires a lot of groundwork, commitment and planning. Success is achieved with a good team where everyone has their own role. Good partnership is setting a common goal and working towards it.","rohkea":"DARE TO GROW","rohkea-p":"Behind successful growth is a good plan of action that helps to achieve the set goals. Growth is only possible, if challenging goals are set and results measured. What is also needed is the courage to make innumerable decisions and the courage to learn from their consequences. ","yhdessa":"POWER THROUGH PARTNERSHIP","yhdessa-p":"Nobody is born to win; they learn to win","taustaa":"BACKGROUND","taustaa-p":"Mad Booster LLC began practise in October 2017, when the former owners and key personnel of Ecom LLC founded a new company. Ecom LLC grew annually by approximately 20% for twenty years and operated very profitably all throughout.","taustaa-p1":"Ecoms founders gave up their remaining ownership of the company in the autumn of 2016. In the same year, Ecom LLC was listed by Kauppalehti as the most successful company in Southwest Finland, being also the most successful software company in Finland.","taustaa-p2":"The strength of Mad Boosters work is in its people. Diversity is an asset that makes the team stronger.","taustaa-p3":"The best teams are created by positive people. Most important is the skill in what one does and the will to become even better.","taustaa-p4":"We are an active party in all of our projects, persistently developing new, thriving businesses. We can be the driving force of these companies, or alternatively support the work of the actual entrepreneur as a shareholder","taustaa-p5":"In Mad Booster, in addition to software and related support services, we handle the successful development of business as well.","avainhenkilot":"KEY PERSONNEL","avain-subhead":"The strength of Mad Booster is in its people.","joni-p":"Joni is the CEO and the chairman of the board of Mad Booster. He is actively searching for new development projects of which the team can develop new innovations for the needs of customers. Joni has over 30 years of experience on the IT field, as he founded his first company already at the age of 16. Before Mad Booster, Joni worked as the CEO and in the board of Ecom Oy for over 20 years. He is also an experienced programmer, although he has now realized to leave coding to the younger people. Besides Mad Booster, Joni also works as an active investor in diverse and different sized projects. ","email":"Email","phone":"Phone","juha-p":"Juha is responsible for the tasks of daily production and is a contact person between clients and the development of Mad Booster. He has working experience from three decades. Juha, too, has worked as a key person in Ecom Oy, being responsible for e.g. key account management and support services over the course of almost fifteen years. He became familiar with location technology when he worked for PPCT Finland Oy. Juha also has education experience in teaching offer calculation and project management at Tampere University of Applied Sciences.","tomi-p":"Tomi works as the Chief Technology officer of Mad Booster, his area of responsibility being the converting of ideas and visions into products and production. Tomi administrates the organization of work in all Mad Boosters development branches. As a technically skilled person Tomi handles everything from databases of the server to user interfaces. He has experience from Ecoms development team, where he was a part of developing e.g. a mobile system. Tomi has a degree of Master of Science in Information Technology.","tomi-phone":"Tomi can be reached by email","jarmo-p":"Jarmo is currently working as the CEO of FC TPS Turku Oy. He is also a background influencer in Mad Booster, and brings people together through his extensive network of contacts. Up until 2016, Jarmo worked as Ecoms Director of Sales and Customer Relations.","kyosti-p":"Kysti is the grand old man of corporate acquisition, the founder of Mergertum and the advisor of Mad Booster. In addition to acquisition processes, he is in charge of jurisdiction and the management of economic affairs. Kystis expertise focuses on corporate acquisition of the operators of the construction, property maintenance and ICT industries, and listing them on the Stock Exchange. He has a history of working in the position of the CEO of Visma, as well as a Banking Lawyer and Vice Judge of the Court. Kysti has arranged hundreds of corporate acquisitions via Mergertum.","contact-us":"CONTACT US","visit-add":"VISITING ADDRESS","bill-add":"BILLING ADDRESS","invoices":"E-INVOICES","getting":"GETTING HERE"}

