import React, { Component } from 'react';
import '../../taustaa.css';
import { getTranslate, getActiveLanguage, getLanguages, setActiveLanguage } from 'react-localize-redux';
import { connect } from 'react-redux';


class Taustaa extends Component {


    render() {
        return (
            <div>
            <div className="container taustaa-head">
                <h2>{this.props.translate('taustaa')}</h2>
                <hr />
                <p> 
                {this.props.translate('taustaa-p')}  <br /><br />

                    {this.props.translate('taustaa-p1')}
                </p>
            </div>
            <div class="row exp-info">
            <div className="information">
            <div className="col-sm-3 exp-box">
                <p>MEILL TYSKENTELEE </p> <br />
                <h3>11 </h3> <br />     
                <p>HENKIL </p> <br />
            </div>
            <div className="col-sm-3 exp-box">
                <p>JOISTA TUOTEKEHITYKSESS </p> <br />
                <h3>7 </h3> <br />     
                <p>KOODARIA </p> <br />
            </div>
            <div className="col-sm-3 exp-box">
                <p>TUOTANNOSSA NYT</p> <br />
                <h3 >6 </h3> <br />     
                <p>PROJEKTIA </p> <br />
            </div>
            <div className="col-sm-3 exp-box">
                <p>TOIMINTA ALOITETTU</p> <br />
                <h3 >2017 </h3> <br />     
                <p>LOKAKUSSA</p> <br />
            </div>
            </div>        
            </div>
            <div className=" container taustaa-btm">
            <p> 
            {this.props.translate('taustaa-p2')} <br /><br />

                {this.props.translate('taustaa-p3')} <br /><br /> 

                {this.props.translate('taustaa-p4')}<br /><br />
                {this.props.translate('taustaa-p5')}<br /><br />
            </p>
            </div>
 
            </div>
             );
             }
      }
                            
        const mapStateToProps = state => ({
        translate: getTranslate(state.locale),
        currentLanguage: getActiveLanguage(state.locale).code,
        languages: getLanguages(state.locale)
    });
    const mapDispatchToProps = { setActiveLanguage };
    
    //export default connect()(Navbar);
    export default connect(mapStateToProps, mapDispatchToProps, null, {pure:false})(Taustaa);


