import React, { Component } from 'react';
import { getTranslate, getActiveLanguage, getLanguages, setActiveLanguage } from 'react-localize-redux';
import { connect } from 'react-redux';

import '../../tavoitteet.css';


class Tavoitteet extends Component {


    render() {
        return (
            <div>
                
                <div className="container tavoitteet">

                <p>  {this.props.translate('background-1')} <br /><br />

                    

                    {this.props.translate('background-2')} <br /><br />

                    

                    {this.props.translate('background-3')}
                </p>
                </div>    
            </div>
             );
             }
      }
      const mapStateToProps = state => ({
        translate: getTranslate(state.locale),
        currentLanguage: getActiveLanguage(state.locale).code,
        languages: getLanguages(state.locale)
    });
    const mapDispatchToProps = { setActiveLanguage };
                            
      export default connect(mapStateToProps, mapDispatchToProps, null, {pure:false})(Tavoitteet);



