import React, { Component } from 'react';
import { BrowserRouter as Router, Route } from "react-router-dom";
import Navbar from "./components/Navbar";
import Video from "./components/Video";
import Footer from "./components/Footer";
import Tavoitteet from "./components/Tavoitteet";
import Missio from "./components/Missio";
import Yritys from "./components/Yritys";
import Taustaa from "./components/Taustaa";
import Yhteystiedot from "./components/Yhteystiedot";
import Henkilot from "./components/Henkilot";
import Rekry from "./components/Rekry";

import { createStore, combineReducers } from 'redux';
import { localeReducer, initialize, addTranslationForLanguage } from 'react-localize-redux';
import { Provider } from 'react-redux';

const enLangJson = require('../lang/en.json');
const fiLangJson = require('../lang/fi.json');


const store = createStore(combineReducers({
    locale: localeReducer
}));

const languages = [
    { name: 'Finnish', code: 'fi'},
    { name: 'English', code: 'en'}
    
];

store.dispatch(initialize(languages, { defaultLanguage: 'fi'}));
store.dispatch(addTranslationForLanguage(enLangJson, 'en'));
store.dispatch(addTranslationForLanguage(fiLangJson, 'fi'));

class App extends Component {



    render() {

        return (
            <Provider store={ store }>
                <Router>
                    
                <div className="wrapper">
                    
                    <Navbar></Navbar>
                    <Route exact path="/" component={Video}  />
                    <Route exact path="/" component={Tavoitteet}  />
                    <Route exact path="/missio" component={Missio} />
                    <Route exact path="/taustaa" component={Taustaa} />
                    <Route exact path="/yritys" component={Yritys} />
                    <Route exact path="/henkilot" component={Henkilot} />
                    <Route exact path="/yhteystiedot" component={Yhteystiedot} />
                    <Route exact path="/rekry" component={Rekry}  />
                    <Footer></Footer>
                </div>
                </Router>
            </Provider>
        );
    }
}

export default App;



